body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
html{
  scroll-behavior: smooth;

}
.scrollDiv{
  height:100vh;
  transition:opacity 500ms ease-in-out;
  opacity:0.2;
}
.scrollDiv.scrolled{
  opacity:1;
}

.App {
  text-align: center;
}
.details{
  background-color: rgb(242,242,242);
}
.App-header {
  background-color: rgb(209, 170, 129);
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  
  color: white;
}
.App-header p{
 margin: 0px 0px 0px 0px;
 padding-top: 10px;
 align-items: center;
 font-family: 'Berkshire Swash', cursive;
 color:black;
 font-weight: bolder;
 font-size: 48px;
}
.App-link {
  color: #61dafb;
}
.about p{
  font-size:xx-large;
  font-family: 'Special Elite', cursive;
  color: darkgreen;
}
.profile-pic img{
  width: 70%;
  height: 70%;
  border-radius: 90%;
  display: inline;
  margin: 100px 20px 20px 40px;
  
  
}
.profile-pic{
  max-width: 500px;
  max-height: 500px;
}

.quote{


  height: 360px;
  width: 360px;
  padding-top: 5%;
  padding-right: 3%;
}



@media screen and (max-width: 768px) { body,p{ font-size: 36px; } }
@media screen and (min-width: 1600px) { body,p{ font-size: 40px; } }



ul{
    list-style-type: none;
    margin: 0;
    padding: 0;
   
}

li {
    float: left;
  }
 li a {
    display: block;
  color: white;
  text-align: center;
  padding: 0px 30px 10px 10px;
  text-decoration: none;
  
  }
  
  nav {
    height: 35px;
    display: flex;
    padding: 20px 30px;
    box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
    background-color: rgb(209, 170, 129);
}
li p{
    
    font-size: 24px;
    font-family: 'Cedarville Cursive', cursive;
    margin: 0px 0px 0px 0px;
    font-weight: bolder;
    color: black;
    padding: 0px 30px 0px 10px;
    
    
}
li:last-child{
  position: absolute;
  right:0;
}
@media screen and (max-width: 768px) { body,p  { font-size: 24px; } }
@media screen and (min-width: 1600px) { body,p{ font-size: 40px; } }

